import React from 'react';
import './Menu.css';

import wolf from './wolf.png';
import menu from './menu.png';
 
class Menu extends React.Component{
    constructor(){
        super()
        this.state = {
          displayMenu: false,
        };
        this.showMenu = this.showMenu.bind(this);
      }
    showMenu(){
        this.setState({
           displayMenu: !this.state.displayMenu
          });
       }
    render(){
        const { displayMenu } = this.state;
        return(
            <div>
                <div className='menu-icon' id='menu' onClick={this.showMenu}>
                    <img alt='menu' src={menu}></img>
                </div>
                <div className={displayMenu ? "Menu Menu-open" : "Menu"} id='Menu'>
                    <a href='#Home' onClick={this.showMenu}><img alt='wolf' src={wolf}></img></a>
                    <ul>
                        <li><a href='#Skills' onClick={this.showMenu}>WHO I AM?</a></li>
                        <li><a href='#Projects' onClick={this.showMenu}>PROJECTS</a></li>
                        <li><a href='#Footer' onClick={this.showMenu}>CONTACT</a></li>
                    </ul>
                </div>
            </div>
        )
    }
}
export default Menu;